// ==========================================================================
// Sticky Footer Layout
// ==========================================================================

body {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

main {
  flex: 1 0 auto; // Grow to fill available space
}

// ==========================================================================
// Footer Styles
// ==========================================================================

footer {
  // Layout
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-shrink: 0;
  
  // Spacing
  padding: 2rem 1rem;
  margin-top: 4rem;
  
  // Typography
  font-size: $footer-font-size;
  text-align: center;
  
  // Visual
  width: 100%;
  background: transparent;
  
  // Responsive padding
  @media (max-width: 768px) {
    padding: 1.5rem 1rem;
    font-size: 1.1rem;
  }
}

// ==========================================================================
// Footer Container
// ==========================================================================

.footer-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  padding: 3rem 2.5rem;
  text-align: center;
  max-width: 800px;
  margin: 0 auto;

  // Light glassmorphic styling
  background: rgba(255, 255, 255, 0.08);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 24px;
  box-shadow: 
    0 8px 32px rgba(0, 0, 0, 0.08),
    inset 0 1px 0 rgba(255, 255, 255, 0.3);

  @media (min-width: $brk-point-md) {
    padding: 1rem 20rem;
  }
}

// ==========================================================================
// Back to Top Component
// ==========================================================================

.back-to-top {
  @include frosted-scroll-btn(70px, 1.8rem);
  
  a {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    text-decoration: none;
    color: inherit;
    font-size: 0.8rem;
    font-weight: 500;
    gap: 0.2rem;
  }
  
  .icon-keyboard_arrow_up {
    color: inherit;
    
    @media (prefers-reduced-motion: reduce) {
      transition: none;
    }
  }
  
  @media (max-width: 768px) {
    width: 60px;
    height: 60px;
    
    .icon-keyboard_arrow_up {
      font-size: 1.5rem;
    }
    
    a {
      font-size: 0.7rem;
    }
  }
}

// ==========================================================================
// Footer Text Styles
// ==========================================================================

.footer-text {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  
  .created-by {
    font-size: 1.1rem;
    color: rgba(255, 255, 255, 0.9);
    font-weight: 500;
    
    .highlight {
      color: rgba(255, 0, 212, 0.9);
      font-weight: 600;
    }
  }
  
  .copyright {
    font-size: 0.9rem;
    color: rgba(255, 255, 255, 0.7);
    
    .year {
      font-weight: 500;
    }
  }
  
  @media (min-width: $brk-point-md) {
    .created-by {
      font-size: 1.2rem;
    }
    
    .copyright {
      font-size: 1rem;
    }
  }
}

// ==========================================================================
// Smooth Scroll Enhancement (if supported)
// ==========================================================================

@supports (scroll-behavior: smooth) {
  html {
    scroll-behavior: smooth;
  }
}

// ==========================================================================
// Print Styles
// ==========================================================================

@media print {
  footer {
    display: none; // Hide footer when printing
  }
}